import React from 'react';
import { Link } from 'react-router-dom';
import { Badge } from 'antd';
import { ColumnsType } from 'antd/es/table';
import { Action, PreviewColumn } from 'components';

/**
 * 
 */
export const createColumns = ({ del, menuData = {} }: any) => {
  const columns: ColumnsType = [
    {
      title: '',
      dataIndex: 'menu_id',
      width: 200,
      ellipsis: true,
      render: (v) => menuData[v],
    },
    {
      title: '',
      dataIndex: 'title',
      width: 200,
      ellipsis: true,
    },
    {
      title: '',
      dataIndex: 'icon',
      width: 60,
      render: (v) => <PreviewColumn src={v} name="" />,
    },
    {
      title: '',
      dataIndex: 'picture_group',
      width: 60,
      render: (v) => <PreviewColumn src={v} name="" type="imageGroup" />,
    },
    {
      title: '',
      dataIndex: 'summary',
      width: 200,
      ellipsis: true,
    },
    {
      title: '',
      dataIndex: 'content',
      width: 200,
      ellipsis: true,
    },
    { title: '', dataIndex: 'create_username', width: 120 },
    { title: '', dataIndex: 'update_username', width: 120 },
    { title: '', dataIndex: 'create_date', width: 180 },
    { title: '', dataIndex: 'update_date', width: 180 },
    { title: '', dataIndex: 'hot', width: 90, fixed: 'right' },
    { title: '', dataIndex: 'priority', width: 90, fixed: 'right' },
    { title: '', dataIndex: 'status', width: 90, fixed: 'right', render: (v) => renders.status[v] },
    {
      title: '',
      key: 'action',
      width: 120,
      fixed: 'right',
      render: (data) => (
        <Action>
          <Link className="edit pointer" to={`/info/info/${data.id}`}>
            
          </Link>
          <span className="delete pointer" onClick={() => del([data.id])}>
            
          </span>
        </Action>
      ),
    },
  ];

  return columns;
};

export const options = {
  status: ['', ''],
};

export const renders = {
  status: [<Badge status="default" text="" />, <Badge status="success" text="" />],
};
