import React, { useState, useCallback } from 'react';
import { ConfigProvider, Drawer, Switch, Tooltip } from 'antd';
import { SettingOutlined } from '@ant-design/icons';
import { Form, FormItem, RadioButton, InputNumber } from 'common/antd';
import { combine } from 'stores';

export const Setting = combine(({ stores }) => {
  const { setting, setSetting } = stores.layout;
  const [visible, setVisible] = useState(false);

  const onShow = useCallback(() => setVisible(true), []);
  const onClose = useCallback(() => setVisible(false), []);

  return (
    <>
      <Tooltip placement="bottom" title="">
        <SettingOutlined className="pointer" onClick={onShow} />
      </Tooltip>
      <Drawer title="" onClose={onClose} visible={visible}>
        <ConfigProvider componentSize="small">
          <Form initialValues={setting} onValuesChange={setSetting}>
            <FormItem label="" name="componentSize">
              <RadioButton options={componentSizeOptions} />
            </FormItem>
            <FormItem label="" name="theme">
              <RadioButton options={themeOptions} />
            </FormItem>
            <FormItem label="" name="hiddenMenu" valuePropName="checked">
              <Switch />
            </FormItem>
            <FormItem label="" name="hiddenHeader" valuePropName="checked">
              <Switch />
            </FormItem>
            <FormItem label=" Top" name="menuIconTop" placeholder>
              <InputNumber min={0} />
            </FormItem>
            <FormItem label=" Right" name="headerIconRight" placeholder>
              <InputNumber min={0} />
            </FormItem>
          </Form>
        </ConfigProvider>
      </Drawer>
    </>
  );
});

const componentSizeOptions = [
  { label: '' },
  { label: '', value: 'large' },
  { label: '', value: 'middle' },
  { label: '', value: 'small' },
];

const themeOptions = { dark: '', light: '' };

/**
 * 
 */
export type TSetting = {
  componentSize: 'large' | 'middle' | 'small'; // 
  theme: 'light' | 'dark'; // 
  hiddenMenu: boolean; // 
  hiddenHeader: boolean; // 
  menuIconTop: number; // 
  headerIconRight: number; // 
};
