import React from 'react';
import { Typography } from 'antd';
import { ColumnsType } from 'antd/es/table';
import { PreviewColumn } from 'components';

const { Text } = Typography;

/**
 * 
 */
export const createColumns = ({ del }: any) => {
  const columns: ColumnsType = [
    {
      title: '',
      key: 'preview',
      width: 60,
      fixed: 'left',
      render: ({ url: src, type, name }) => <PreviewColumn {...{ src, type, name }} />,
    },
    {
      title: '',
      dataIndex: 'name',
      width: 200,
      render: (v) => (
        <Text ellipsis copyable>
          {v}
        </Text>
      ),
    },
    {
      title: '',
      dataIndex: 'url',
      width: 300,
      render: (v) => (
        <Text ellipsis copyable>
          {v}
        </Text>
      ),
    },
    { title: '', dataIndex: 'type', width: 90, render: (v: keyof typeof options.type) => options.type[v!] },
    { title: '', dataIndex: 'store', width: 90, render: (v: keyof typeof options.store) => options.store[v!] },
    { title: '', dataIndex: 'size', width: 120 },
    { title: '', dataIndex: 'username', width: 120 },
    { title: '', dataIndex: 'create_date', width: 180 },
    {
      title: '',
      key: 'action',
      width: 60,
      fixed: 'right',
      render: (data) => (
        <span className="delete pointer" onClick={() => del([data.id])}>
          
        </span>
      ),
    },
  ];

  return columns;
};

export const options = {
  type: { image: '', video: '', audio: '', other: '' },
  store: { local: '', oss: 'OSS' },
};
