import React from 'react';
import { Badge } from 'antd';
import { ColumnsType } from 'antd/es/table';
import { Action, PreviewColumn } from 'components';

/**
 * 
 */
export const createColumns = ({ add, edit, del }: any) => {
  const columns: ColumnsType = [
    {
      title: '',
      dataIndex: 'title',
      width: 400,
    },
    {
      title: '',
      dataIndex: 'icon',
      width: 60,
      render: (v) => <PreviewColumn src={v} name="" />,
    },
    {
      title: '',
      dataIndex: 'content',
      width: 200,
      ellipsis: true,
    },
    { title: '', dataIndex: 'create_username', width: 120 },
    { title: '', dataIndex: 'update_username', width: 120 },
    { title: '', dataIndex: 'create_date', width: 180 },
    { title: '', dataIndex: 'update_date', width: 180 },
    { title: '', dataIndex: 'priority', width: 90, fixed: 'right' },
    { title: '', dataIndex: 'status', width: 80, fixed: 'right', render: (v) => renders.status[v] },
    {
      title: '',
      key: 'action',
      width: 210,
      fixed: 'right',
      render: (data) => (
        <Action>
          <span className="success pointer" onClick={() => add({ pid: data.id })}>
            
          </span>
          <span className="edit pointer" onClick={() => edit(data)}>
            
          </span>
          <span className="delete pointer" onClick={() => del([data.id])}>
            
          </span>
        </Action>
      ),
    },
  ];

  return columns;
};

export const options = {
  status: ['', ''],
};

export const renders = {
  status: [<Badge status="default" text="" />, <Badge status="success" text="" />],
};
