import React from 'react';
import { message } from 'antd';
import { FormItem, Radio, InputNumber, TextArea } from 'common/antd';
import { PageForm, useInfo, UploadInput } from 'components';
import { combinePage } from 'stores';
import { options } from './config';
import { info } from 'apis';
import { SelectMenu } from '../menu/select';

type TParams = { id?: string };

const Page = combinePage<TParams>(({ history, match }) => {
  const { id } = match.params;
  const { setData, setLoading, pageFormProps } = useInfo({
    defaultData: { hot: 0, priority: 0, status: 1 },
    getData: async () => {
      if (!id) return;
      setLoading('');
      const res = await info.details(id);
      res.ok && setData(res.data);
      setLoading(false);
    },
    onFinish: async (values: any) => {
      setLoading('');
      if (id) values.id = id; //  ID
      const res = await info[id ? 'edit' : 'add'](values);
      if (!res.ok) return setLoading(false);
      message.success(`${id ? '' : ''}`);
      history.goBack();
    },
  });

  return (
    <PageForm {...pageFormProps}>
      <FormItem label="" name="menu_id" placeholder select required>
        <SelectMenu />
      </FormItem>
      <FormItem label="" name="title" placeholder required />
      <FormItem label="" name="summary" placeholder>
        <TextArea />
      </FormItem>
      <FormItem label="" name="content" placeholder>
        <TextArea />
      </FormItem>
      <FormItem label="" name="icon">
        <UploadInput />
      </FormItem>
      <FormItem label="" name="picture_group">
        <UploadInput multiple max={20} />
      </FormItem>
      <FormItem label="" name="hot" required>
        <InputNumber />
      </FormItem>
      <FormItem label="" name="priority" required>
        <InputNumber />
      </FormItem>
      <FormItem label="" name="status">
        <Radio options={options.status} />
      </FormItem>
    </PageForm>
  );
});

export default Page;
