import React from 'react';
import { Tag, Badge } from 'antd';
import { ColumnsType } from 'antd/es/table';
import { Action, PreviewColumn } from 'components';

/**
 * 
 */
export const createColumns = ({ edit, del }: any) => {
  const columns: ColumnsType = [
    {
      title: '',
      dataIndex: 'avatar',
      width: 60,
      render: (v) => <PreviewColumn src={v} name="" />,
    },
    { title: '', dataIndex: 'username', width: 120 },
    { title: '', dataIndex: 'nickname', width: 160 },
    { title: 'IP', dataIndex: 'reg_ip', width: 160 },
    { title: 'IP', dataIndex: 'login_ip', width: 160 },
    { title: '', dataIndex: 'create_date', width: 180 },
    { title: '', dataIndex: 'login_date', width: 180 },
    {
      title: '',
      dataIndex: 'type',
      width: 90,
      fixed: 'right',
      render: (v: keyof typeof renders.type) => renders.type[v],
    },
    { title: '', dataIndex: 'status', width: 90, fixed: 'right', render: (v) => renders.status[v] },
    {
      title: '',
      key: 'action',
      width: 120,
      fixed: 'right',
      render: (data) => (
        <Action>
          <span className="edit pointer" onClick={() => edit(data)}>
            
          </span>
          <span className="delete pointer" onClick={() => del([data.id])}>
            
          </span>
        </Action>
      ),
    },
  ];

  return columns;
};

export const options = {
  type: { admin: '', user: '' },
  status: ['', '', ''],
};

export const renders = {
  type: {
    admin: <Tag color="orange"></Tag>,
    user: <Tag color="green"></Tag>,
  },
  status: [
    <Badge status="default" text="" />,
    <Badge status="success" text="" />,
    <Badge status="error" text="" />,
  ],
};
