import { get, post, put, del, upload, baseURL } from 'common/request';

/**
 * 
 */
export const auth = {
  login: (data: any) => post('/auth/admin', data, ''),
  getInfo: (Authorization: string) => get('/auth', {}, '', { noMessage: true, headers: { Authorization } }),
};

/**
 * 
 */
export const uploadFile = {
  upload: (file: File) => upload('/upload', { file }, ''),
  install: (data: any) => post('/upload/install', data, ''),
  getList: (data: any) => get('/upload', data, ''),
  del: (ids: string[]) => del('/upload', { ids }, ''),
  getUploadUrl: (type = '') => `${baseURL}/upload/${type}`,
};

/**
 * 
 */
export const account = {
  getList: (data: any) => get('/account', data, ''),
  add: (data: any) => post('/account', data, ''),
  edit: ({ id, ...data }: any) => put(`/account/${id}`, data, ''),
  del: (ids: string[]) => del('/account', { ids }, ''),
};

/**
 * 
 */
export const menu = {
  getList: (data?: any) => get('/menu', data, ''),
  add: (data: any) => post('/menu', data, ''),
  edit: ({ id, ...data }: any) => put(`/menu/${id}`, data, ''),
  del: (ids: string[]) => del('/menu', { ids }, ''),
};

/**
 * 
 */
export const info = {
  getList: (data: any) => get('/info', data, ''),
  details: (id: string) => get(`/info/${id}`, {}, ''),
  add: (data: any) => post('/info', data, ''),
  edit: ({ id, ...data }: any) => put(`/info/${id}`, data, ''),
  del: (ids: string[]) => del('/info', { ids }, ''),
};
